@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap');


body {
    font-family: 'Roboto', sans-serif;
    color: #666;
    background: #fff;
    font-size:16px;
}


.form-Field{
  width:95%;
  margin:30px auto 0;
}

.fieldSet-input{
  border-radius: 4px;
  padding:5px 0;
  margin:0 0 4px 0;
  overflow:hidden;
}

.fieldSet-input_active{
  border-radius: 4px;
  border:2px solid #e74b18;
  padding:5px 0;
  margin:0 0 10px 0;
  overflow:hidden;
}

.form-Control{
  width:100%;
  padding:8px;
  margin:0;
  outline:none;
  border:none;
  font-size:16px;
}

legend{
  transition: all 1.5s ease;
  padding:0;
  margin:0 0 0 12px;
  font-size: 13px;
  color:#e74b18;
}

.btn_Field{
  width:93%;
  margin:20px auto 10px;
  cursor:pointer;
}

.btn_button{
  background-color:#e74b18;
  border:none;
  border-radius: 4px;
  color:#fff;
  font-weight: bold;
  padding:10px;
  width:100%;
  letter-spacing: 0.1rem;
  cursor:pointer;
}

.btn_button:hover{
  background-color: #333;
}